body{
	width: 100%;
}
.app{
	position: relative;
	top: 70px;
}
.loading-icon{
	position: relative;
	top: 200px;
}
.title{
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    	"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
  	-moz-osx-font-smoothing: grayscale;
