	padding: 7px 10px 10px 7px;
	text-align: center;
	border-bottom: 1px solid  #cccccc;
}
.buttons{
	margin-bottom: -37px;
	position: relative;
	left: 280px;
	top: -39.5px;
}
.btn{
	border-radius:  0px;
}
